// src/components/Step4Confirmation.js
import { useEffect } from "react";
import confetti from "canvas-confetti";

export default function Step4Confirmation({ formData, submissionStatus }) {
    useEffect(() => {
        if (submissionStatus?.success) {
            confetti({
                particleCount: 100,
                spread: 70,
                origin: { y: 0.6 },
                colors: ["#f05d23", "#231812", "#d94f1e"],
            });
        }
    }, [submissionStatus]);

    return (
        <div className="text-center animate-fade-in-up">
            <svg
                className="w-16 h-16 mx-auto mb-4 text-[#f05d23] animate-bounce"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"
                />
            </svg>
            <h2 className="text-3xl font-bold text-[#231812] mb-4">Thank You, {formData.fullName}!</h2>
            <p className="text-lg text-[#231812] mb-4">Your application has been successfully submitted.</p>
            <p className="text-xl font-semibold text-[#f05d23] mb-6">Your Score: {submissionStatus.score}/190</p>
            <p className="text-sm text-[#231812] max-w-md mx-auto">
                We’ve sent a confirmation email to {formData.email}. Please check your inbox (and spam folder) for further details.
            </p>
        </div>
    );
}