// src/components/DragDropZone.js
import { Icon } from "@iconify/react";

export default function DragDropZone({
                                         type,
                                         file,
                                         isDragging,
                                         uploadProgress,
                                         isSubmitting,
                                         handleFileChange,
                                         handleDrop,
                                         handleDragOver,
                                         handleDragLeave,
                                         removeFile,
                                     }) {
    return (
        <div
            onDrop={(e) => handleDrop(e, type)}
            onDragOver={(e) => handleDragOver(e, type)}
            onDragLeave={(e) => handleDragLeave(e, type)}
            className={`p-6 flex justify-center bg-white border-2 border-dashed rounded-xl transition-all duration-300 transform ${
                isDragging ? "border-[#f05d23] bg-orange-50 scale-105" : "border-gray-300"
            }`}
        >
            {!file ? (
                <div className="text-center">
                    <span className="inline-flex justify-center items-center w-12 h-12 bg-gray-100 text-[#f05d23] rounded-full mb-4">
                        <Icon icon="mdi:upload" className="w-6 h-6" />
                    </span>
                    <div className="text-sm text-gray-600">
                        <span className="font-medium text-[#231812]">
                            Drop your {type === "resume" ? "Resume" : "Cover Letter"} here or{" "}
                        </span>
                        <input
                            type="file"
                            name={type}
                            accept=".pdf,.docx"
                            onChange={(e) => handleFileChange(e, type)}
                            className="hidden"
                            id={`${type}-upload`}
                        />
                        <label
                            htmlFor={`${type}-upload`}
                            className="font-semibold text-[#f05d23] hover:text-[#d94f1e] cursor-pointer transition"
                        >
                            browse
                        </label>
                    </div>
                    <p className="mt-1 text-xs text-gray-400">PDF or DOCX, max 5MB</p>
                </div>
            ) : (
                <div className="w-full p-4 bg-gray-50 border border-gray-300 rounded-xl shadow-sm">
                    <div className="flex justify-between items-center mb-2">
                        <div className="flex items-center gap-x-3">
                            <span className="w-10 h-10 flex justify-center items-center border border-gray-200 text-[#f05d23] rounded-lg">
                                {file.name.endsWith(".pdf") ? (
                                    <Icon icon="mdi:file-pdf" className="w-5 h-5" />
                                ) : (
                                    <Icon icon="mdi:file-word" className="w-5 h-5" />
                                )}
                            </span>
                            <div>
                                <p className="text-sm font-medium text-[#231812] truncate max-w-[200px]">{file.name}</p>
                                <p className="text-xs text-gray-500">{(file.size / 1024).toFixed(2)} KB</p>
                            </div>
                        </div>
                        <button
                            onClick={() => removeFile(type)}
                            className="text-gray-500 hover:text-[#f05d23] transition"
                        >
                            <Icon icon="mdi:trash-can-outline" className="w-5 h-5" />
                        </button>
                    </div>
                    {isSubmitting && (
                        <div className="flex items-center gap-x-3">
                            <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
                                <div
                                    className="h-full bg-[#f05d23] rounded-full transition-all duration-500"
                                    style={{ width: `${uploadProgress[type]}%` }}
                                />
                            </div>
                            <div className="w-10 text-end">
                                <span className="text-sm text-[#231812]">{uploadProgress[type]}%</span>
                            </div>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}