import Image from "next/image";

const Header = ({ mode, toggleMode, step, currentPage, totalPages, uploadProgress, answeredQuestions, totalQuestions, isStep1Complete }) => {
    let progressPercentage = 0;
    let progressText = "";

    if (step === 1) {
        // Step 1: 5% when complete, 0% otherwise
        progressPercentage = isStep1Complete ? 5 : 0;
        progressText = `${progressPercentage}% Done`;
    } else if (step === 2 && totalPages > 0) {
        // Step 2: Start at 5%, add 90% (5% to 95%) based on answered questions
        progressPercentage = 5 + Math.round((answeredQuestions / totalQuestions) * 90);
        progressText = `${progressPercentage}% Done`;
    } else if (step === 3) {
        // Step 3: Start at 95%, add 5% (2.5% resume + 2.5% cover letter)
        const resumeProgress = uploadProgress?.resume || 0;
        const coverLetterProgress = uploadProgress?.coverLetter || 0;
        const resumeContribution = (resumeProgress / 100) * 2.5;
        const coverLetterContribution = (coverLetterProgress / 100) * 2.5;
        progressPercentage = 95 + resumeContribution + coverLetterContribution;
        progressText = `${Math.round(progressPercentage)}% Done`;
    }

    return (
        <header
            className="bg-[#fff8f7] border-[#231812] border-b shadow-lg text-white py-4 md:py-6 px-4 md:px-10 flex items-center sticky top-0 z-50">
            <div className="flex justify-around items-center w-full">
                <div className="flex-shrink-0">
                    <Image
                        src={mode === "dark" ? "/assets/images/logo-white.svg" : "/assets/images/logo.svg"}
                        alt="Logo"
                        width={240}
                        height={40}
                        className="w-32 md:w-[200px]"
                    />
                </div>
                {(step === 1 || step === 2 || step === 3) && (
                    <div className="w-1/2 relative">
                        <div className="w-full bg-gray-200 rounded-lg h-10">
                            <div
                                className="bg-[#f05d23] h-10 px-8 rounded-lg transition-all duration-300 flex items-center justify-center text-white text-base font-semibold"
                                style={{width: `${progressPercentage}%`}}
                            >
                                {progressText}
                            </div>
                        </div>
                    </div>
                )}
            </div>

            <div className="flex items-center space-x-2">
                <button
                    onClick={toggleMode}
                    className="p-2 focus:outline-none md:hidden"
                    aria-label="Toggle dark mode"
                >
                    {mode === 'dark' ? <SunIcon className="h-6 w-6"/> : <MoonIcon className="h-6 w-6"/>}
                </button>
                <label className="hidden md:inline-flex items-center cursor-pointer">
                    <input type="checkbox" checked={mode === 'dark'} onChange={toggleMode} className="hidden"/>
                    <div
                        className={`relative w-14 h-8 rounded-full border-2 flex items-center ${
                            mode === 'dark' ? 'border-blue-600 bg-blue-600' : 'border-gray-300 bg-gray-300'
                        } transition`}
                    >
                        <div
                            className={`absolute w-6 h-6 rounded-full bg-white flex items-center justify-center transition-transform ${
                                mode === 'dark' ? 'translate-x-6' : ''
                            }`}
                        >
                            {mode === 'dark' ? (
                                <MoonIcon className="h-6 w-6 text-gray-700"/>
                            ) : (
                                <SunIcon className="h-6 w-6 text-yellow-500"/>
                            )}
                        </div>
                    </div>
                </label>

                <div className="flex items-center gap-2 relative group cursor-default" ref={dropdownRef}
                     onClick={() => setDropdownOpen(!dropdownOpen)}>
                    <div className="flex items-center gap-2 cursor-pointer">
                        <div className="w-10 h-10 rounded-full overflow-hidden">
                            <Image
                                src={user?.profile_image || '/assets/images/placeholder.png'}
                                alt="User Profile"
                                width={48}
                                height={48}
                                className="object-cover"
                            />
                        </div>
                        <div className="hidden md:block">
                                <span
                                    className={`font-bold ${mode === 'dark' ? 'text-white' : 'text-[#403F41]'}`}>{fullName}</span>
                            <span
                                className={`block text-sm font-normal ${mode === 'dark' ? 'text-[#28A8E0]' : 'text-[#28A8E0]'}`}>Member</span>
                        </div>
                        <Icon
                            icon={dropdownOpen ? "bxs:up-arrow" : "bxs:down-arrow"} // Conditional icon based on dropdownOpen state
                            className={`h-5 w-5 font-bold transform transition-transform duration-300 ${mode === 'dark' ? 'text-white' : 'text-[#403F41]'}`}
                        />
                    </div>

                    {dropdownOpen && (
                        <div
                            className={`absolute top-full mt-2 right-0 w-80 rounded-2xl shadow-lg z-10 ${
                                mode === 'dark' ? 'bg-gray-800 text-white' : 'bg-white text-black'
                            } z-50`}
                            onClick={(e) => e.stopPropagation()}
                        >
                            <div className="p-8">
                                <p className="text-lg mb-6">User Profile</p>
                                <div
                                    className="flex items-center gap-2 border-b pb-6 w-full transition-all duration-500 ease-out transform hover:-translate-y-[10px]">
                                    <div className="rounded-full overflow-hidden flex-shrink-0">
                                        <Image
                                            src={user?.profile_image || '/assets/images/placeholder.png'}
                                            alt="User Profile"
                                            width={40}
                                            height={40}
                                            className="object-cover"
                                        />
                                    </div>
                                    <div className="flex flex-col">
                                            <span
                                                className={`text-md font-bold ${mode === 'dark' ? 'text-white' : 'text-black'}`}>{fullName}</span>
                                        <span className="text-sm">Customer</span>
                                        <div className="flex items-center justify-center gap-2">
                                            <EnvelopeIcon className="h-4 w-4"/>
                                            <span className="text-sm">{user?.email || 'N/A'}</span>
                                        </div>
                                    </div>
                                </div>

                                <Link href="/profile">
                                    <div
                                        className="flex gap-2 capitalize py-6 transition-all duration-500 ease-out transform hover:-translate-y-[10px]">
                                        <LockClosedIcon
                                            className="bg-[#e7f8f7] text-[#28A8E0] rounded-full p-2 h-10 w-10"/>
                                        <div className="flex flex-col">
                                            <span className="text-md font-bold">My Profile</span>
                                            <span className="text-sm">Account Settings</span>
                                        </div>
                                    </div>
                                </Link>

                                <button
                                    onClick={onLogout}
                                    className={`block w-full text-center text-white px-4 py-2 bg-[#28A8E0] rounded-full transition-all duration-500 ease-out transform hover:-translate-y-[10px] ${
                                        mode === 'dark' ? 'hover:bg-gray-600' : 'hover:bg-[#8DC63F]'
                                    }`}
                                >
                                    Logout
                                </button>
                            </div>
                        </div>
                    )}
                </div>

            </div>
        </header>
    );
};

export default Header;